const initValue = {
  isLogged: true,
  loginError: false,
  trackedCities: [],
};

const dataReducer = (state = initValue, action) => {
  let editedState;
  switch (action.type) {
    case "LOGIN":
      if (
        action.payload.login === "admin" &&
        action.payload.password === "admin"
      )
        return { ...state, isLogged: true, loginError: false };

      return { ...state, isLogged: false, loginError: true };

    case "LOGOUT":
      return { ...state, isLogged: false };

    case "ADD_CITY":
      editedState = {
        ...state,
        trackedCities: [...state.trackedCities, action.payload],
      };
      return editedState;

    case "DELETE_CITY":
      editedState = {
        ...state,
        trackedCities: state.trackedCities.filter(
          (e) => e.id !== action.payload
        ),
      };
      return editedState;
    default:
      return state;
  }
};

export default dataReducer;
